/* Reset & Global */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(to right, #2e2e2e, #4f4f4f);
  color: #333;
}


/* Main Container */
.maindiv {
  display: flex;
  max-width: 1300px;
  margin: 0 auto;
  padding: 30px;
  gap: 40px;
}

/* Left Panel - Fixed */
.leftdiv {
  position: fixed;
  top: 30px;
  left: 30px;
  width: 670px;
  height: calc(100vh - 60px);
  overflow-y: auto;
  background: linear-gradient(to right, #8B0000, #B22222);
  padding: 30px;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  z-index: 10;

  /* Hover Transition */
  transition: background-color 0.4s ease, transform 0.3s ease;
}

/* Hover Effect */
.leftdiv:hover {
  background: linear-gradient(to right, #8B0000, #B22222); /* Light blue on hover */
  transform: scale(1.03); /* Slight zoom */
}

/* Scrollbar for Left */
.leftdiv::-webkit-scrollbar {
  width: 6px;
}
.leftdiv::-webkit-scrollbar-thumb {
  background-color: #ccc;
  border-radius: 8px;
}
.leftdiv::-webkit-scrollbar-track {
  background: transparent;
}

/* Left Text Styling */
.name {
  font-size: 2.2rem;
  color: #fbfbfb;
  font-weight: bold;
  margin-bottom: 10px;
}

.leftdiv p {
  font-size: 1rem;
  margin-bottom: 20px;
  color: #fffdfd;
  line-height: 1.6;
}

.leftdiv hr {
  border: none;
  border-top: 1px solid #ddd;
  margin: 20px 0;
}

/* Right Content Section */
.rightdiv {
  margin-left: 750px;
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 30px;
  margin-bottom: 70px;
}

/* Ball Style for smalldivs */
.smalldivs {
  background: linear-gradient(to right, #8B0000, #B22222);
  width: 430px;
  height: 430px;
  border-radius: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 30px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  transform: perspective(200px) rotateX(15deg) rotateY(10deg);
  transition: all 0.3s ease;
  cursor: pointer;
  text-align: center;
  overflow: hidden;
  margin-bottom: 60px;
}

/* Hover Effect for Balls */
.smalldivs:hover {
  transform: perspective(200px) rotateX(0deg) rotateY(0deg) scale(1.05);
  box-shadow: 0 12px 35px rgba(0, 0, 0, 0.3);
}

/* Ball Title */
.smalldivs h2 {
  font-size: 1.5rem;
  color: #fff;
  margin-bottom: 10px;
  font-weight: bold;
}

.smalldivs hr {
  border: none;
  border-top: 1px solid #fff;
  margin-bottom: 12px;
}

.smalldivs ul {
  list-style: none;
  padding-left: 0;
}

.smalldivs li {
  position: relative;
  padding-left: 20px;
  margin-bottom: 10px;
  font-size: 1.1rem;
  color: #fff;
}

.smalldivs li::before {
  content: "âœ”";
  position: absolute;
  left: 0;
  color: #fff;
  font-size: 1.1rem;
}

/* Responsive Design */
@media screen and (max-width: 1024px) {
  .maindiv {
    flex-direction: column;
    padding: 20px;
  }

  .leftdiv {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: auto;
    margin-bottom: 20px;
    transform: none;
  }

  .rightdiv {
    margin-left: 0;
    align-items: center;
  }

  .smalldivs {
    width: 280px;
    height: 280px;
  }
}
